import React from 'react'
import axios from 'axios'
import './categoriesPage.css'
import Header from '../../../public/header/index'
import Footer from '../../../public/footer/index'
import Cookies from 'js-cookie'


export default function Categories(props) {
    const { id } = props.match.params
    const redirectToTopic = (pk, type) => {
        switch (type) {
            case 'theme':
                window.location = `/forum/${pk}`;
            break;
            case 'link':
                window.location.href = pk;
            break;
            case 'topic':
                window.location = `/forum/topic/${pk}`;
            break;
            default:
                return;
        }
        
    }
    const [content, setContent] = React.useState({
        mainCategory: {
            categoryID: 0,
            categoryName: "",
            description: "",
            isComment: false,
        },
        categories: [
            {
                procreator: {
                    category: 0
                },
                categoryID: 6,
                categoryName: "",
                description: "",
                isComment: false
            }
        ],
        links: [{
            name: "",
            link: "",
        }],
        topics: [{
            isClosed: false,
            topicID: 0,
            date: "",
            name: "",
            content: "",
            procreator: 0,
            ownerID: "",
            count: 0
        }]
    });
    React.useEffect(() => {
        axios.get(`/api/forum/${id}`).then(res => {
            setContent(res.data)
        })
    })
    const redirectToCreateThheme = (e) => {
        e.preventDefault()
        window.location = `/forum/${id}/create`
    }
    return (
        <>
        <Header />
        <div className='categories container'>
            <div className="categories-page">
                {!content.mainCategory.description ?
                <div className="catigories-header d-flex">
                    <div className="categories-name">{content.mainCategory.categoryName}</div>
                    {Cookies.get('user') ? 
                    <div className="categories-add">
                        <button className="btn btn-success" onClick={redirectToCreateThheme}>+ Вопрос</button>
                    </div> : null}
                </div> :
                <div className="catigories-header">
                    <div className="catigories-header-main d-flex">
                        <div className="categories-name">{content.mainCategory.categoryName}</div>
                        {Cookies.get('user') ? 
                            <div className="categories-add">
                                <button className="btn btn-success" onClick={redirectToCreateThheme}>+ Вопрос</button>
                            </div> : null}
                        </div>
                        <div className="catogories-main-text">{content.mainCategory.description}</div>
                    </div>}

                    <div className="catigories-full">
                        {content.categories && content.categories.map((category, i) => (
                        <div className="category" key={i} onClick={e => redirectToTopic(category.categoryID, 'theme')}>
                            <div style={{ cursor: 'pointer'}} >
                                <div className="row">
                                <div className="forum-logo-module-forum col-1" style={{marginBottom: '.5rem', marginLeft: '.5rem'}}><i className="bi bi-bookmark-star"></i></div>
                                    <div className="forum-name col-xl-8 d-flex col-lg-7 col-md-6 col-10">
                                        {/* {category.isComment ? <div className="status status-closed">Закрыт</div> : null} */}
                                        {category.categoryName}
                                    </div>
                                    <div className="forum-logo col-2 text-center" style={{color: '#fff'}}>{category.count}<div>Категорий</div></div>
                                </div>
                            </div>
                        </div>))}
                        {content.links && content.links.map((link, i) => (
                        <div className="category" key={i} onClick={e => redirectToTopic(link.link, 'link')}>
                            <div style={{ cursor: 'pointer'}} >
                                <div className="row">
                                <div className="forum-logo-module-forum col-1" style={{marginLeft: '.5rem', marginBottom: '.4rem', fontSize: '27px'}}><i className="bi bi-link-45deg"></i></div>
                                    <div className="forum-name col-xl-8 d-flex col-lg-7 col-md-6 col-10">
                                        {link.name}
                                    </div>
                                </div>
                            </div>
                        </div>))}
                        {content.topics && content.topics.map((topic, i) => (
                        <div className="category" key={i} onClick={e => redirectToTopic(topic.topicID, 'topic')}>
                            <div style={{ cursor: 'pointer'}} >
                                <div className="row">
                                <div className="forum-logo-module-forum col-1" style={{marginBottom: '.5rem', marginLeft: '.5rem'}}><i className="bi bi-chat-right-text"></i></div>
                                    <div className="forum-name col-xl-8 d-flex col-lg-7 col-md-6 col-10">
                                        {topic.isClosed ? <div className="status status-closed">Закрыт</div> : null}
                                        {topic.name}
                                    </div>
                                    <div className="forum-logo col-2 text-center" style={{color: '#fff'}}>{topic.count}<div>Сообщений</div></div>
                                </div>
                            </div>
                        </div>))}

                    </div>
                 </div>
            </div>
        <Footer />
        </>
    )
}