import React from 'react'
import './index.css'
import Header from '../public/header/index'
import ModuleForum from './components/ModuleForum/ModuleForum'
import Footer from '../public/footer/index'
import RightColumn from './components/RightColumn/RightColumn'

export default function Forum() {
    return (
        <>
        <Header />
        <div className="forum container d-flex">
            <div className="forum-module">
            <ModuleForum name="Главный раздел" color="rgb(88, 92, 102)" />
            <ModuleForum name="Игровой сервер" color="rgb(88, 92, 102)" />
            <ModuleForum name="RolePlay" color="rgb(88, 92, 102)" />
            <ModuleForum name="Прочее" color="rgb(88, 92, 102)" />
            </div>
            <RightColumn />
            
        </div>
        <Footer />
        </>
    )
}