import axios from 'axios'
import React from 'react'
import Cookies from 'js-cookie'
import './index.css'
import star_logo from './logo.png'

export default function Header() {
     const [state, setState] = React.useState()

    React.useEffect(() => {
        const user = Cookies.get('user');
        if (user) {
            axios.get(`/api/profile/${user}`).then((resp) => {
                const allPersons = resp.data;
                setState(allPersons);
            });
        }
    }, [setState]);

      const unlogin = () => {
          Cookies.remove('user')
          window.location.reload()
      }

    const redirectToProfile = () => window.location = `/profile/${state.userID}`
    return (
        <div className="navbar">
            <div className="navbar-body container">
                <div className="navbar-right row">
                    {/* <a href='/'> */}
                    <a href='/' className="navbar-logo col-5">
                        <img src={star_logo} alt="Logo" className='rotated' />
                    </a>
                    <div className="navbar-text col-5">
                        <a href="/forum" className='navbar-text-forum'><div>ФОРУМ</div></a>
                        <a href="/users" className='navbar-text-users'><div>ПОЛЬЗОВАТЕЛИ</div></a>
                    </div>
                </div>
                <div className="navbar-left">
                    {state ? 
                    <div className="navbar-success-login d-flex">
                        <div className="navbar-avatar" onClick={redirectToProfile}>
                            <img src={state.avatarLast} alt={state.username} />
                        </div>
                        <div className="unlogin mt-2" onClick={unlogin}>
                            <button className="btn btn-danger">Выйти</button>
                        </div>
                    </div>
                     : 
                     <div className="navbar-login">
                        <a href="/auth">
                            <button className="btn btn-success">Войти</button>
                        </a>
                    </div>
                    
                    }
                </div>
            </div>
        </div>
    )
}