import React from 'react'
import axios from 'axios'
import './createcategories.css'
import Header from '../../../public/header/index'


export default function Forum(props) {
    const { name: theme } = props.match.params
    const [name, setName] = React.useState('')
    const [description, setDescription] = React.useState('')
    const [checkbox, setCheckbox] = React.useState(false)
    const [select, setSelect] = React.useState('none');
    

    const handleSubmit = (e) => {
        if (select === 'link') {
            e.preventDefault()
            axios.post('/forum/link/create',
            { name, link: description, parent: theme}).then((res) => {
                if (res.status === 200) {
                    window.location = `/`;
                }
            })
        } else {
            e.preventDefault()
            axios.post(`/forum/add`, {name, description, checkbox, parent: theme})
                .then(res => {
                    if (res.status === 200) {
                        window.location = `/forum/${res.data.id}`
                    }
                })
        }
    }
    const handleType = () => {
        switch (select) {
            case 'theme':
                return (
                    <div className="forum-create-categories d-flex mt-5">
                        <form className="form-signin text-center m-auto" onSubmit={handleSubmit}>
                        <div className="mb-3">Название<input className="form-control" onChange={e => setName(e.target.value)} /></div>
                        <div className="mb-3">Описание<textarea className="form-control" row="5" maxLength={300} onChange={e => setDescription(e.target.value)} /></div>
                        <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault" onChange={e => setCheckbox(e.target.checked)} />
                            <label className="form-check-label" for="flexCheckDefault">
                                Запрещены комментарии
                            </label>
                        </div>
                        <input type="submit" className="btn btn-success mt-5" value="Создать" />
                        </form>
                    </div>
                )
            case 'link':
                return (
                    <div className="forum-create-categories d-flex mt-5">
                        <form className="forum-create form-signin m-auto text-center" onSubmit={handleSubmit}>
                            <div className="mb-3">Название<input className="form-control" onChange={e => setName(e.target.value)} /></div>
                            <div className="mb-3">Ссылка<input className="form-control" row="5" maxLength={300} onChange={e => setDescription(e.target.value)} /></div>
                            <input type="submit" className="btn btn-success mt-5" value="Создать" />
                        </form>
                    </div>
                );
            default:
                return;
        }
    }
    return (
        <>
        <Header />
        <div className="forum-header-text text-center mt-5">Создание Темы</div>
        <select className="form-select d-flex m-auto mt-5" onChange={v => setSelect(v.target.value)}>
            <option value="none" selected>Выберите тип темы</option>
            <option value="theme">Создать Категорию</option>
            <option value="link">Создать Ссылку</option>
        </select>
        {handleType()}
        </>
    )
}