var BBTag = require('bbcode-parser/bbTag');
var BBCodeParser = require('bbcode-parser');

var bbTags = {};

bbTags["img"] = BBTag.createTag("img", function (tag, content, attr) {
    return "<img src=\"" + content + "\" />";
})

bbTags["i"] = BBTag.createSimpleTag("i");
bbTags["b"] = BBTag.createSimpleTag("b");
bbTags["code"] = BBTag.createSimpleTag("code");
bbTags["ul"] = BBTag.createSimpleTag("ul");
bbTags["li"] = BBTag.createSimpleTag("li");

bbTags["text"] = BBTag.createTag("text", (tag, content, attr) => {
    let args = ""
    if (attr["color"]) {
        args += `color: ${attr["color"]};`
    }
    if (attr["size"]) {
        args += `font-size: ${attr["size"]}px;`
    }
    return `<span style="${args}">${content}</span>`
})

bbTags["url"] = BBTag.createTag("url", function (tag, content, attr) {
    var link = content;

    if (attr["site"] !== undefined) {
        link = BBCodeParser.escapeHTML(attr["site"]);
 	}

    if (!BBCodeParser.startsWith(link, "http://") && !BBCodeParser.startsWith(link, "https://")) {
        link = "http://" + link;
    }

    return "<a href=\"" + link + "\" target=\"_blank\">" + content + "</a>";
});

bbTags["center"] = BBTag.createTag("center", function (tag, content, attr) {
    return `<div class="text-center">${content}</div>`;
});

function bbcode(text) {
    return new BBCodeParser(bbTags).parseString(text);
}

module.exports = { bbcode };