import React from 'react'
import axios from 'axios'
import './createTheme.css'
import Header from '../../../public/header/index'
import Cookies from 'js-cookie'
import { bbcode } from '../../../Profile/BBcode'


export default function CreateTheme(props) {
    const { id: categoryID } = props.match.params
    const [name, setName] = React.useState('')
    const [user, setUser] = React.useState({ isAdmin: false });
    const [content, setContent] = React.useState('')
    const [select, setSelect] = React.useState('none');
    const [checkbox, setCheckbox] = React.useState(false);
    const handleSubmit = (e) => {
        e.preventDefault()
        switch (select) {
            case 'theme':
                axios.post(`/forum/add`, 
                { name, description: bbcode(content), checkbox, parent: Number(categoryID)}).then((res) => {
                    if (res.status === 200) {
                        window.location = `/forum/${categoryID}/topic/${res.data.id}`;
                    }
                })
            break;
            case 'link':
                axios.post('/forum/link/create',
                {name, link: content, parent: Number(categoryID)}).then((res) => {
                    if (res.status === 200) {
                        window.location = `/forum/${categoryID}`;
                    }
                })
            break;
            case 'topic':
                axios.post('/forum/topic/create',
                {name, isComment: checkbox || false, content: bbcode(content), ownerID: Cookies.get('user'), procreator: categoryID}).then((res) => {
                    if (res.status === 200) {
                        return window.location = `/forum/${categoryID}/topic/${res.data.id}`;
                    } 
                });
            break;
            default:
                return;
        }
    }
    React.useEffect(() => {
        axios.get(`/api/profile/${Cookies.get('user')}`).then((res) => {
            if (res.status === 200) {
                return setUser(res.data)
            }
            return window.location = '/';
        })
    })
    const handleType = () => {
        switch (select) {
            case 'theme':
                return (
                    <div className="forum-create-categories d-flex mt-5">
                        <form className="forum-create form-signin m-auto text-center" onSubmit={handleSubmit}>
                        <div className="mb-3">Название<input className="form-control" onChange={e => setName(e.target.value)} /></div>
                        <div className="mb-3">Описание<textarea className="form-control" row="5" maxLength={300} onChange={e => setContent(e.target.value)} /></div>
                        <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault" onChange={e => setCheckbox(e.target.checked)} />
                            <label className="form-check-label" htmlFor="flexCheckDefault">
                                Запрещены комментарии
                            </label>
                        </div>
                        <input type="submit" className="btn btn-success mt-5" value="Создать" />
                        </form>
                    </div>
                )
            case 'link':
                return (
                    <div className="forum-create-categories d-flex mt-5">
                        <form className="forum-create form-signin m-auto text-center" onSubmit={handleSubmit}>
                            <div className="mb-3">Название<input className="form-control" onChange={e => setName(e.target.value)} /></div>
                            <div className="mb-3">Ссылка<input className="form-control" row="5" maxLength={300} onChange={e => setContent(e.target.value)} /></div>
                            <input type="submit" className="btn btn-success mt-5" value="Создать" />
                        </form>
                    </div>
                );
            case 'topic':
                return (
                    <div className="forum-create-categories d-flex mt-5">
                        <form className="forum-create form-signin m-auto text-center" onSubmit={handleSubmit}>
                        <div className="mb-3">Название<input className="form-control" onChange={e => setName(e.target.value)} /></div>
                        <div className="mb-3">Описание<textarea className="form-control" row="5" maxLength={300} onChange={e => setContent(e.target.value)} /></div>
                        <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault" onChange={e => setCheckbox(e.target.checked)} />
                            <label className="form-check-label" htmlFor="flexCheckDefault">
                                Запрещены комментарии
                            </label>
                        </div>
                        <input type="submit" className="btn btn-success mt-5" value="Создать" />
                        </form>
                    </div>
                )
            default:
                return;
        }
    }
    return (
        <>
        <Header />
        <div className="theme-create-header-text text-center mt-5" style={{fontSize: '40px', color: '#fff'}}>Создание Темы</div>
        <select className="form-select d-flex m-auto mt-5" onChange={v => setSelect(v.target.value)}>
            <option value="none" defaultValue>Выберите тип темы</option>
            {user && user.isAdmin ? (
                <>
                    <option value="theme">Создать Категорию</option>
                    <option value="link">Создать Ссылку</option>
                </>
            ) : null}
            
            
            <option value="topic">Создать Топик</option>
        </select>
        {handleType()}
        </>
    )
}