import React from 'react'
import Cookies from 'js-cookie'
import './header.css'
import '../Chat/chat.css'
import '../LeftColumn/leftColumn.css'
import clock_icon from '../../clock.svg'
import letter_icon from '../../letter.png'
import Footer from '../../../public/footer/index'
import axios from 'axios'
import { bbcode } from '../../BBcode'

export default function HeaderProfile(props) {
    const [textarea, setTextarea] =React.useState('')
    const [user, setUser] = React.useState({
        badges: ['none'],
        dateRegister: '',
        countMessages: 0,
        isBan: false,
        isAdmin: false,
        userID: '',
        username: '',
        avatar: '',
        avatarLast: '',
        dateNormal: '',
        messages: []
    });
    
    React.useEffect(() => {
        axios.get(`/api/profile/${props.header.match.params.id}`).then((resp) => {
          if (resp.data.response === true) {
                setUser(resp.data)
          }
        });
    }) 
    const handleSubmit = (event) => {
        event.preventDefault();
        axios.post(`/message/user/add/${props.header.match.params.id}`, 
            {message: bbcode(textarea), date: new Date().toLocaleString()}
        ).then(res => {  
            window.location.reload()
        })
      }
      const deleteMessage = async (id) => {
        await axios.delete(`/message/delete/${id}`)
        window.location.reload()
      }
    return (
        <div className="profile">
            <main className="container min-100-vh">
                <div className="profile-center">
                    <div className="profile-background">
                        <div className="profile-avatar d-flex">
                            <img src={user.avatarLast} alt="Avatar" className="mt-3" />
                            <div className="profile-nickname mt-2">{user.username}</div>
                        </div>
                    </div>
                    <div className="profile-bottom-div">
                        <div className="icons d-flex">
                        <div className="icon-message mt-1">
                            <img src={letter_icon} alt="Messages" />
                            <div className="counter-message">{user.messages.length}</div>
                        </div>
                        <div className="icon-clock mt-2">
                            <img src={clock_icon} alt="Messages" />
                            <div className="counter-clock">{user.dateNormal}</div>
                        </div>
                        </div>
                    </div>
                </div>
                <div className="profile-header-chat d-flex justify-content-between">
                    {/* Left Column */}
                    {/* <BadjesList lsit={user.badges} /> */}
                    <div className="left-column">
                        {/* {user.isAdmin ?
                            user.badges.map(type => (
                            <>
                                <div className={`role d-flex justify-content-between ${type[0]}`}>
                                    <i className={`role ${type[1]}`}></i> {type[2]}
                                    <div className="btn-delete"><button className="btn btn-danger">-</button></div>
                                </div>
                                <div className="button-add d-flex" onClick={clickAdd}>
                                    <button className="btn btn-success m-auto mt-3">+ Admin</button>
                                </div>
                            </>
                            ))
                            :
                            user.badges.map(type => (
                                <div className={`role ${type[0]}`}>
                                    <i className={`role ${type[1]}`}></i> {type[2]}
                                </div>
                            ))
                        } */}
                        {user.isAdmin ?
                        <div className="role coder">
                            <i className="bi bi-gear"></i> Разработчик
                        </div> : 
                        <div className="role none">Пользователь</div>}
                    </div>
                    

                    {/* Chat */}
                <div className="chat">
                    {Cookies.get('user') === user.userID ?
                    <form onSubmit={handleSubmit}>
                        <textarea className="chat-write-area" placeholder="Что нового?" maxLength={300} cols="30" rows="10" onChange={e => setTextarea(e.target.value)}></textarea>
                        <div className="chat-bottom-area">
                            <div className="chat-button-post">
                                <input type="submit" className="btn btn-success" value='Отправить' />
                            </div>
                        </div>
                    </form> :
                    null
                    }
                    <div className="messages">
                    {user.messages && [...user.messages].reverse().map(mess => (
                        <div className="message" key={mess.messageID}>
                            <div className="message-user-info d-flex justify-content-between">
                                <div className="message-header d-flex">
                                    <div className="message-avatar">
                                        <img src={user.avatarLast} alt="Avatar" />
                                    </div>
                                    <div className="message-nickname">
                                        <div className="message-user-username">{user.username}</div>
                                    </div>
                                </div>
                                {Cookies.get('user') === user.userID ?
                                (
<div className="message-trash" style={{marginLeft: '2rem', cursor: 'pointer'}} onClick={e => deleteMessage(mess.messageID)}><i className="bi bi-trash"></i></div>
                                ) : null}
                            </div>
                            <div className='post' dangerouslySetInnerHTML={{__html: mess.content}}></div>
                            <div className="date text-muted mt-4">{mess.dateNormal}</div>
                        </div>
                    ))}      
                </div>
            </div>
        </div>
    </main>
    <Footer />
    </div>
    )
}


