import React from 'react'
import './topic.css'
import Header from '../../../public/header/index'
import Footer from '../../../public/footer/index'
import axios from 'axios'
import Cookies from 'js-cookie'
import { bbcode } from '../../../Profile/BBcode'



export default function CreateTheme(props) {
    const { id } = props.match.params
    const [message, setMessage] = React.useState('')
    const [isAdmin, setIsAdmin] = React.useState(false)
    const [content, setContent] = React.useState({
        topic: {
            isClosed: false,
            isComment: false,
            topicID: 0,
            date: "23.09.2021",
            name: "",
            content: "",
            procreator: 0,
            ownerID: "",
            owner: {
                avatarLast: "",
                dateRegister: "2021-09-23T08:58:06.661Z",
                dateNormal: "23.09.2021",
                isBan: false,
                isAdmin: false,
                userID: "",
                username: "",
                avatar: "3949e65e6f74ca82f3aef5749f8a9c7b",
            }
        },
        comment: []
    })
    React.useEffect(() => {
        axios.get(`/api/topic/${id}`).then(res => {
            setContent(res.data)
        })
    })
    React.useEffect(() => {
        axios.get(`/api/profile/${Cookies.get('user')}`).then(res => {
            setIsAdmin(res.data.isAdmin)
        })
    })
    const handleSubmit = async (e) => {
        await axios.post('/topic/message/add',
        { userID: Cookies.get('user'), content: bbcode(message), topicID: id }).then((res) => {
            if (res.status === 200) {
                window.location.reload()
            }
        })
    }
    const closeTopic = () => {
        axios.get(`/api/topic/close/${id}`).then((res) => {
            if (res.status === 200) {
                window.location.reload();
            }
        })
    }
    const deleteTopic = () => {
        axios.delete(`/topic/delete/${id}`).then((res) => {
            if (res.status === 200) {
                window.location.href = "/forum"
            }
        })
    }
    const redirectToProfile = (id) => window.location = `/profile/${id}`
    return (
        <div>
        <Header />
        <div className="topic container">
            <div className="topic-header-row row ">
                
                <div className="topic-header col-xxl-2 col-xl-2 col-lg-3 col-md-3 col-sm-4">
                {/* <a href={() => redirectToProfile(topic.ownerID)}> */}
                    <img src={content.topic.owner.avatarLast}
                     alt='Logo' className="m-auto" style={{ cursor: 'pointer'}} onClick={() => redirectToProfile(content.topic.ownerID)} />
                    <div className="topic-header-owner-nickname">{content.topic.owner.username}</div>
                    {content.topic.owner.isAdmin ? 
                        <div className="topic-header-owner-role topic-moderator">Модератор</div> :
                        <div className="topic-header-owner-role">Пользователь</div>
                    }
                    <div className="message-date mt-3">{content.topic.date}</div>
                    {Cookies.get('user') && isAdmin && !content.topic.isClosed ?
                        <div className="topic-close-topic">
                            <button className="btn btn-danger mt-3" onClick={() => closeTopic()}>Закрыть Топик</button>
                        </div> : null}
                        {content.topic.isClosed ?
                        <div className="topic-close"><span>Закрыто</span></div> : null}
                        {Cookies.get('user') && isAdmin ?
                        <div className="topic-system">
                            <div className="topic-system-delete" onClick={() => deleteTopic()}>
                            <button type="button" className="btn btn-outline-danger"><i className="bi bi-trash"></i> Удалить топик</button>
                            </div>
                        </div> : null}
                </div>
                <div className="topic-main-message col-xxl-9 col-xl-9 col-lg-7 col-md-9 col-sm-7" style={{fontSize: '35px'}}>{content.topic.name}<div style={{fontSize: '20px'}} dangerouslySetInnerHTML={{__html: content.topic.content}}></div></div>
            </div>
        <div className="topic-messages">
        {content.comment && content.comment.map((mess, i) => (
                    <div key={i} className="topic-message">
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <a style={{ cursor: 'pointer'}} onClick={() => redirectToProfile(mess.userID)}><div className="message-header d-flex">
                            <div className="message-avatar">
                                <img src={mess.user.avatarLast ?? 'https://imgur.com/9lh1TO9.jpeg'} alt="Avatar" />
                            </div>
                        {mess.user.isAdmin ? 
                        <div className="message-nickname topic-moderator">{mess.user.username ?
                            mess.user.username : "Deleted User"}</div> :
                        <div className="message-nickname" style={{textTransform: "none"}}>{mess.user.username}</div>}
                    </div></a>
                    <div className='post' dangerouslySetInnerHTML={{__html: mess.content}}></div>
                    <div className="date text-muted mt-4">{mess.dateNormal}</div>
                </div>
                ))}
        </div>
        {!content.topic.isClosed && !content.topic.isComment && Cookies.get('user') ?
        <div className="topic-chat">
            <form onSubmit={handleSubmit}>
                <textarea className="topic-chat-write-area" placeholder="Что нового?" maxLength={300} cols="30" rows="10" onChange={e => setMessage(e.target.value)}></textarea>
                <div className="topic-chat-bottom-area">
                    <div className="topic-chat-button-post">
                        <input type="submit" className="btn btn-success" value='Отправить' />
                    </div>
                </div>
            </form>
        </div>
        : null}
        </div>
        <Footer />
        </div>
    )
}