import Profile from './components/Profile/index'
import Forum from './components/Forum/index'
import NotFound from './components/NotFound/notFound'
import ForumAdd from './components/Forum/components/CreateCategories/CreateCategories'
import Categories from './components/Forum/components/CategoriesPage/CategoriesPage'
import CreateTheme from './components/Forum/components/CreateTheme/CreateTheme'
import Topic from './components/Forum/components/Topic/Topic'
import React from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

function App() {  
  return (
    <>
    <Router>
      <Switch>
        <Route path="/profile/:id" component={Profile} />
        {/* <Route path="/forum/add" component={ForumAdd} /> */}
        <Route path="/forum/categories/add/:name" component={ForumAdd} />
        <Route path="/forum/topic/:id" component={Topic} />
        <Route path="/forum/:id/create" component={CreateTheme} />
        <Route path="/forum/:id" component={Categories} />
        <Route path="/forum" component={Forum} />
        <Route path="/users" component={NotFound} />
        <Route path="/" component={Forum} />
        <Route component={NotFound} />
      </Switch>
    </Router>
    </>
  );
}
// Category --> Themes --> Topic
// Category --> Category --> Themes --> Topic
export default App;
