import React from 'react'
import './moduleForum.css' 
import Cookies from 'js-cookie'
import axios from 'axios'

export default function ModuleForum({ name, color }) {
    const adminRedirect = () => window.location = `/forum/categories/add/${name}`
    const redirectTo = (pk, type) => {
        switch (type) {
            case 'theme':
                window.location = `/forum/${pk}`;
            break;
            case 'link':
                window.location.href = pk;
            break;
            default:
                return;
        }
    }
    const [content, setContent] = React.useState({
        mainCategory: {
            categoryID: 0,
            categoryName: "",
            description: "",
            isComment: false,
            count: 0,
        },
        categories: [
            {
                procreator: {
                    category: 0
                },
                categoryID: 6,
                categoryName: "",
                description: "",
                isComment: false
            }
        ],
        links: [{
            name: "",
            link: "",
        }],
        topics: [{
            isClosed: false,
            topicID: 0,
            date: "",
            name: "",
            content: "",
            procreator: 0,
            ownerID: "",
        }]
    });
    const [user, setUser] = React.useState({ isAdmin: false });
    React.useEffect(() => {
        axios.get(`/api/profile/${Cookies.get('user')}`).then((resp) => {
          if (resp.data.response) {
                setUser(resp.data)
          }
        });
    }, [setUser])
    React.useEffect(() => {
        axios.get(`/api/forum/categories/${name}`).then(res => {
            setContent(res.data)
        })
    });
    return (
        <div className="forum-window"> 
        {user.isAdmin ?
            <div className="forum-window-header d-flex justify-content-between" style={{background: color}}>
                <div>{name}</div>
                <div className="btn btn-success" onClick={adminRedirect}>+</div>
            </div> :  
            <div className="forum-window-header" style={{background: color}}>
                <div>{name}</div>
            </div>
        }
            {/*  */}
            {content.categories && content.categories.map((category, i) => (
            <div key={i}>
            <div className="forum-line" style={{ cursor: 'pointer'}} onClick={e => redirectTo(category.categoryID, 'theme')}>
                <div className="row">
                    <div className="forum-logo-module-forum col-1"><i className="bi bi-bookmark-star"></i></div>
                    <div className="forum-name col-xl-8 d-flex col-lg-7 col-md-6 col-8">
                        {/* <div className="status status-warning">Важно</div> */}
                        {category.categoryName}
                    </div>
                    <div className="forum-last-user col-4 col-xl-3 text-center">
                        {/* <img src={avatar_logo} alt="Avatar" />
                        <div className="forum-last-nickname mt-2"><a href="/">Vivers</a> в {cat.lastMessageTime}</div> */}
                        <div style={{color: '#fff', fontSize: '13px'}}>{category.count}<div>Темы</div></div>
                    </div>
                </div>
            </div>   
            </div>  
            ))}       
            {/*  */}
            {content.links && content.links.map((link, i) => (
            <div key={i}>
            <div className="forum-line" style={{ cursor: 'pointer'}} onClick={e => redirectTo(link.link, 'link')}>
                <div className="row">
                    <div className="forum-logo-module-forum col-1"><i className="bi bi-link-45deg"></i></div>
                    <div className="forum-name col-xl-8 d-flex col-lg-7 col-md-6 col-8">
                        {link.name}
                    </div>
                </div>
            </div>   
            </div>  
            ))}       
            {/*  */}
        </div>
    )
}